body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    background-color:white;
}

.navbar-brand {
    color: rgb(240, 16, 27);
    font-size: 32px;

}

.navbar li {
    display: inline-block;
    color: rgb(240, 16, 27);
}

.navcenter {
    font-size: 32px;
    text-align: center;
}

.score {
    font-size: 24px;
}
.header {
    height: 800px;
    text-align: center;
    background-image: url(/C-game/static/media/wow.4d84f420.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.header h1 {
    padding-top: 35px;
    font-size: 72px;
    font-family: myFirstFont;
    font-weight: bold;
    color: 	white;
    text-shadow:
    -1px -1px 0 #6DC379,
    1px -1px 0 #6DC379,
    -1px 1px 0 #6DC379,
    1px 1px 0 #6DC379;
}

.header p {
    font-size: 26px;
    color: white;
    text-shadow:
    -1px -1px 0 #6DC379,
    1px -1px 0 #6DC379,
    -1px 1px 0 #6DC379,
    1px 1px 0 #6DC379;
}

@media all and (max-width: 800px) { 
    .header h1 {
        padding-top: -40px;
        font-size: 60px; 
    }
    .header p {
        font-size: 24px;
    }
}

@media all and (max-width: 650px) { 
    .header h1 {
        font-size: 48px;
    }
    .header p {
        font-size: 20px;
    }
}

.card {
    height: 200px;
    position: relative;
    width: 175px;
    margin-bottom: 20px;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.48);
}

.card .img-container {
    height: 100%;
    overflow: hidden;
  }

  .card .img-container > img {
    width: 100%;
    height: 200px;
  }
.wrapper {
    padding-top: 50px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    max-width: 1200px;
    margin: auto;
}

@media all and (max-width: 1090px) { 
    .wrapper {
        max-width: 900px;
    }
    
}
